import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
// 
import gsap from 'gsap'

// gui
import * as dat from 'dat.gui'

const scene = new THREE.Scene()

const camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 1000)
camera.position.set(15, 5, 15)
scene.add(camera)

const geometry = new THREE.BoxGeometry( 1, 1, 1 );
const material = new THREE.MeshBasicMaterial( {color: 0xffff00} );
const cube = new THREE.Mesh( geometry, material );
scene.add( cube );

const gui = new dat.GUI()
// 
gui.add(camera.position, 'x').min(0).max(20).step(0.01).name('x').onChange((value) => { console.log(''); }).onFinishChange((value) => { console.log('', value); })
gui.add(camera.position, 'y').min(0).max(20).step(0.01).name('y')
gui.add(camera.position, 'z').min(0).max(20).step(0.01).name('z')

const folder = gui.addFolder('')
const params = {
    color: '#ffff00',
    animation1: () => {
        gsap.to(cube.position, {
            x: 5,
            ease: 'power1.inOut',
            duration: 5,
            repeat: -1,
            yoyo: true,
        })
    },
    animation2: () => {
        gsap.to(cube.rotation, {
            x: 2*Math.PI,
            ease: 'power1.inOut',
            duration: 5,
            repeat: -1,
            yoyo: true,
        })
    }
}

folder.add(cube.position, 'x').min(0).max(40).step(0.1).name('x')
folder.add(cube.position, 'y').min(0).max(40).step(0.1).name('y')
folder.add(cube.position, 'z').min(0).max(40).step(0.1).name('z')
folder.addColor(params, 'color').onChange((value) => {
    cube.material.color.set(value)
})
folder.add(params, 'animation1').name('')
folder.add(params, 'animation2').name('')

const axesHelper = new THREE.AxesHelper( 5 );
scene.add( axesHelper );

const renderer = new THREE.WebGLRenderer()
renderer.setSize(window.innerWidth, window.innerHeight)

const controls = new OrbitControls( camera, renderer.domElement )

document.body.appendChild(renderer.domElement)

window.addEventListener('dblclick', function() {
    if(!this.document.fullscreenElement) {
        renderer.domElement.requestFullscreen();
    } else {
        this.document.exitFullscreen()
    }
})

//  
window.addEventListener('resize', function() {
    // 
    camera.aspect = this.innerWidth / this.innerHeight
    // .updateProjectionMatrix
    camera.updateProjectionMatrix()
    // 
    renderer.setSize(this.innerWidth, this.innerHeight)
    renderer.setPixelRatio(renderer.getPixelRatio())

})
function render() {
    renderer.render(scene, camera)
    requestAnimationFrame(render)
}
render()